import React,{Component} from 'react';
import {Container,Item} from './styled';
import {Link} from 'react-router-dom';
import $ from 'jquery';

import prod from '../../produtos';

export default class Product extends Component {
    
    constructor(props){
        super(props);
        this.state=JSON.parse(localStorage.getItem("prod"));

        
        this.prod = prod;
        
        this.text=[];
        // this.state.id
        // this.products=this.prod[this.state.id];
        this.state ?
        this.state.id.map((item,index)=>{
            this.text.push({
                title:this.prod[item][this.state.key[index]].title,
                group:this.prod[item][0],
                key:this.state.key[index],
            });
        })
        :
        localStorage.removeItem("prod");

        this.sendMessage = this.sendMessage.bind(this);
        this.goBack = this.goBack.bind(this);
         

    }

    render(){

        return <Container>

            <div class="cardButton">
                <button  onClick={()=>window.history.back()} className="btBack"> ➜ </button>
                <button className="enviar" onClick={()=>{this.sendMessage()}}>Enviar Mensagem</button>

            </div>
            
            <header class="card">
            <div className="TitleCar"><span>CARRINHO</span></div>

            <div className="carrinho">

                {
                    this.state ?
                    this.text.map((item)=>{
                    return <div key={item.key} className="boxCar"> 
                                <div className="info">
                                    <div className="grupo">{item.group}</div> 
                                    <div className="title">{item.title}</div> 
                                </div>
                                <div className="remover">Excluir</div>
                            </div>;
                })
                :
                <div className="vazio"> Carrinho vazio ! </div>
                }
            </div>
            </header>
        </Container> ;
    }

    componentDidUpdate(){
        window.location.reload();
    }
    componentDidMount(){
        $(".remover").click((e)=>{

            var newState =[];
            
            let newId = this.state.id.filter((item,i) => { 
                return i !== $(e.target).index()-1;
                    
            });
            let newKey = this.state.key.filter((item,i) => { 
                return i !== $(e.target).index()-1;
            });

            newState={id: newId ,key: newKey};

            console.log(newState.id.length);
            if(newState.id.length !==0)
                localStorage.setItem("prod",JSON.stringify(newState));
            else
                localStorage.removeItem("prod");
            window.location.reload();
        });

        

    }

    sendMessage(){
        let texto="";
        this.text.map((item)=>{
            texto +="*- "+item.title+"*\n";
        });


        let conversa = encodeURI("Olá, prazer eu estava olhando seu produtos pelo site Todabella Manaus e me interessei nesse(s) produto(s):\n"+texto+"\n Eu gostaria de conferir mais informações, avaliações e orçamentos para este(s) produto(s).");
        let _url="";
        if (/Mobi/.test(navigator.userAgent)) {
            _url="https://api.whatsapp.com/send?phone=5592999039215&text="+conversa;
        }
        else{
            _url="https://web.whatsapp.com/send?phone=5592999039215&text="+conversa;
        }

        console.log(_url);
        window.open(_url, '_blank');

    }
    goBack(){
        window.history.back();
    }
}
