import styled from 'styled-components';
import texture from  './../../images/img.jpg';

export const Container = styled.section`
    min-height: 100vh;
    padding: 10px;
    color:var(--textPrimaryColor); 
    position: relative; 
    display:flex;
    flex-direction:column;
    align-items:center;

    :after {
        content: "";
        background: url(${texture});
        background-position:center;
        background-size:120px;
        opacity: 0.25;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        position: absolute;
        z-index: -1;   
    }
    .cardButton{
        display:flex;
        justify-content:space-between;
        align-items:center;
        width:95vw;

        .btBack{
            background-color:white;
            text-decoration:none;
            width:40px;
            height:40px;
            overflow:hidden;
            font-size:30px;
            display:flex;
            justify-content:center;
            align-items:center;
            border-radius:100%;
            text-align:center;
            color:#272727;
            font-weight:bolder;
            transform:rotateY(180deg);
            margin:15px;
            cursor: pointer;
            
        }

        .enviar{
            background-color:#2f8c3c;
            box-shadow:inset 0px -3px rgba(27,27,27,.3);
            height:fit-content;
            padding:15px;
            color:white;
            font-weight:bold;
            border-radius: 50px;
            transition:.3s ease-in;
            :hover{
                transform:translateY(-2px);
                box-shadow:inset 0px -4px rgba(27,27,27,.3);
            }
        }

    }

    .card{
        width:100%;
        max-width:1000px;
        
    }
    .TitleCar{
        background-color:var(--primaryColor);
        width:100%;
        display:flex;
        /* justify-content:center; */

        span{
            padding:15px;
            color:var(--backColor);
            box-shadow:inset 0px -3px rgba(27,27,27);
            font-weight:bolder;
        }
        
    }

    .carrinho{
        min-height:50vh;
        background-color:whitesmoke;
        max-height:120vh;

        .vazio{
            color:tomato;
            background-color:whitesmoke;
            display:flex;
            align-items:center;
            justify-content:center;
            font-size:1.5em;
            text-align:center;
            padding:15px;
            font-weight:bold;
            min-height:60vh;
        }
       
        .boxCar{
            display:grid;
            grid-template-columns: 4fr 1fr;
            box-shadow:inset 0px -2px rgba(27,27,27,.3);
            z-index:2;
            background-color:whitesmoke;
            align-items:center;
            
            

            div{
                
                color:var(--backColor);
                
            }
            .info{
                padding:10px;
                .grupo{
                    color:tomato;
                    text-transform:uppercase;
                    font-weight:bold;
                }
                .title{
                    font-size:1.5em;
                    margin-left:4vw;
                }
            }

            .remover{
                padding:10px;
                height:fit-content;
                display:flex;
                justify-content:center;
                align-items:center;
                background-color:tomato;
                border-radius:50px;
                margin:10px;
                cursor: pointer;
                transition:.3s ease-in;
                box-shadow:inset 0px -2px rgba(27,27,27,.3);
                font-weight:bold;
                :hover{
                    transform:translateY(-2px);
                    box-shadow:inset 0px -4px rgba(27,27,27,.3);
                }
            }
            
            
        }
        .title div{
            color:var(--backColor);
            background-color:greenyellow;
            padding:20px;
        }

    }
`;